.products-card {
  display: flex;
  flex-direction: column;
  padding: 10px;
  box-sizing: border-box;

  &--big {
    @extend .products-card;
    width: 100%;
    .home-collection {
      width: 20vw;
      gap: 20px;
      @media (max-width: 1440px) {
        width: 22.5vw;
        max-width: 100%;
      }

      @media (max-width: 960px) {
        width: 27vw;
      }

      @media (max-width: 760px) {
        width: 28.5vw;
      }

      @media (max-width: $big-phone-size) {
        padding: 5px;
        width: 29vw;
      }

      @media (max-width: $phone-size) {
        width: 43.5vw;
      }
    }
  }

  &--medium {
    @extend .products-card;
    width: 200px;
    gap: 20px;

    @media (max-width: 1260px) {
      width: 150px;
    }

    @media (max-width: 1100px) {
      width: 130px;
    }

    @media (max-width: 960px) {
      width: 150px;
    }
  }

  &--small {
    @extend .products-card;
    width: 166px;
    gap: 10px;
    font-size: clamp(12px, 1.1vw, 18px);

    @media (max-width: 1260px) {
      width: 130px;
    }
  }
}

.products-card-color {
  padding: 5px;
  border: 1px solid $border-black;
  cursor: pointer;
}

.products-card-delete {
  text-align: center;
}

.products-card-color-small {
  width: 30px;
  height: 30px;
  padding: 5px;
  border: 1px solid $border-black;
  position: relative;
  box-sizing: border-box;

  @media (max-width: $mini-tablet-size) {
    width: 25px;
    height: 25px;
  }

  @media (max-width: $big-phone-size) {
    width: 20px;
    height: 20px;
  }

  div {
    width: 100%;
    height: 100%;
    background-color: #D3D3D3;
  }
}

.products-card__bottom-row {
  display: flex;
  gap: 24px;
}
.products-card__counter {
  display: flex;
  gap: 6px;
}

.products-card__delete {
  color: #8c8c8c;
  /* Body 2 */
  font-family: Pragmatica;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%; /* 20.8px */
}

.products-card__counter-input {
  text-align: center;
  box-sizing: border-box;
  height: 20px;
  width: 20px;
  
  &:focus {
      border: none;
      outline: none;
      border-bottom: 1px solid #8c8c8c;
    }
}

.products-card__counter-input,
.products-card__delete,
.products-card__counter-button {
  color: #8c8c8c;
  /* Body 2 */
  font-family: Pragmatica;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%; /* 20.8px */
}

.products-card__counter-button {
  display: flex;
  align-items: center;
}


.products-card-title__product {
  width: 70%;
  @media (max-width: $big-phone-size) {
    width: 100%;
  }
}

.products-card-title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  &--big {
    @extend .products-card-title;
    @media (max-width: 760px) {
      flex-wrap: wrap;
    }
  }

  &--medium {
    @extend .products-card-title;
    @media (max-width: 1020px) {
      .products-card-title__product {
        width: 100%;
        margin-bottom: 10px;
      }
      flex-wrap: wrap;
    }
  }

  &--small {
    @extend .products-card-title;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}

.products-card-title__price {
  color: $text-grey;
  font-size: clamp(12px, 1.1vw, 20px);
}

.home-collection .products-card-list {
  width: 90%;

  @media (max-width: $phone-size) {
    width: 100%;
  }
}

.products-card-container__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;

  @media (max-width: 1440px) and (min-width: 760px) {
    .container {
      max-width: 90vw;
      margin: 0 auto;
    }
  }

  @media (max-width: 760px) {
    max-width: 100vw;
  }
}

.products-card-container__reset-button {
  font-size: 16px;
  transition: color 0.9s;

  &:hover {
    color: $text-grey;
    transition: color 0.9s;
  }
}

.products-card-head {
  display: flex;
  padding: 0 10px;

  &--space-between {
    @extend .products-card-head;
    justify-content: space-between;
  }

  &--centered {
    @extend .products-card-head;
    justify-content: center;

    .title {
      margin-bottom: 15px;
    }
  }
}

.products-card__link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;

  @media (max-width: 760px) {
  }
}

.products-card-list {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  width: 100%;

  @media (max-width: 960px) {
    grid-template-columns: repeat(3, 33.3%);
  }

  @media (max-width: 430px) {
    grid-template-columns: repeat(2, 50%);
  }

  &--2 {
    grid-template-columns: repeat(4, 25%);

    @media (max-width: 1100px) {
      grid-template-columns: repeat(3, 33.3%);
    }
    @media (max-width: 760px) {
      grid-template-columns: repeat(2, 50%);
    }
    @media (max-width: 430px) {
      grid-template-columns: repeat(1, 100%);
    }
  }

  .products-card-list--4 {
    grid-template-columns: repeat(3, 33.3%) !important;
  }

  & li {
    border: 1px solid transparent;
    transition: 0.7s;

    &:hover {
      border-color: rgb(192, 192, 192);
      transition: 0.7s;
    }
  }
}

.products-card__price {
  color: $text-grey;
}

.products-card__colors {
  display: flex;
  gap: 8px;
}
