body {
  .checkout-content {
    .address-right {
      transition: 1.5s;
      overflow-y: scroll;

      &.active {
        .modal-right-content {
          right: 0;
          transition: 1.5s;
        }
      }

      .modal-right-content {
        display: grid !important;
        grid-template-columns: 65% 35% !important;
        padding: 40px 60px 40px 40px !important;
        max-width: 1298px !important;
        width: 100vw !important;
        right: $size-guide-modal-right;
        transition: 1.5s !important;
        box-sizing: border-box;

        @media (max-width: 900px) {
          grid-template-columns: 60% 40% !important;
        }

        @media (max-width: $big-phone-size) {
          grid-template-columns: 100% !important;
          padding: 0 !important;
          padding-top: 20px !important;
        }

        & img {
          width: 100%;
        }

        &.map {
          .address-right__map-mobile {
            max-height: 1000px;
            transition: 0.5s;
            height: max-content;
          }
        }

        &.list {
          .address-content__list {
            max-height: 1000px;
            transition: 0.5s;
          }
        }
      }
    }

    .address-right__map {
      @media (max-width: $big-phone-size) {
        display: none;
      }
    }

    .address-content__controllers {
      display: flex;
      gap: 40px;
      justify-content: center;
      border-bottom: $border-grey 1px solid;
      border-top: $border-grey 1px solid;
      @media (min-width: $big-phone-size) {
        display: none;
      }
    }

    .address-content__controller {
      padding: 12px 0;
      transition: 0.5s;
      border-bottom: transparent 1px solid;

      &.active {
        border-color: $border-black;
        transition: 0.5s;
      }
    }

    .address-right__map-mobile {
      @media (min-width: $big-phone-size) {
        display: none;
      }
      max-height: 0px;
      overflow: hidden;
      transition: 0.5s;
    }

    //Content

    .address-content {
      display: flex;
      flex-direction: column;
      gap: 40px;
      width: 100%;
      box-sizing: border-box;

      & h2 {
        padding-bottom: 10px;
        border-bottom: $border-grey 1px solid;
        margin-bottom: 20px;
      }

      @media (max-width: $big-phone-size) {
        padding: 0 10px;
      }
    }

    //List

    .address-content__list {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-bottom: 20px;

      @media (max-width: $big-phone-size) {
        max-height: 0px;
        overflow: hidden;
        transition: 0.5s;
      }
    }
  }
}
